import { useEffect } from "react";
import { Link } from "react-router-dom";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import AdminNavs from "../../components/Admin/AdminNavs";
import {
  createProduct,
  deleteProduct,
  listProducts,
} from "../../actions/productAction";
import { BiEdit } from "react-icons/bi";
// import styled from 'styled-components'

const AdminProducts = ({ history, match }) => {
  const [keyword, setKeyword] = useState("");
  const pageNumber = match.params.pageNumber || 1;
  const dispatch = useDispatch();
  const { error, loading, products, pages, page } = useSelector(
    (state) => state.listProducts
  );
  const {
    error: errorDelete,
    loading: loadingdelete,
    success,
  } = useSelector((state) => state.productDelete);
  const userLogin = useSelector((state) => state.user);

  useEffect(() => {
    dispatch({ type: "product_create_reset" });
    dispatch(listProducts(keyword));
  }, [dispatch, history, pageNumber, keyword]);

  const deleteHandler = (id) => {
    if (window.confirm("Are you sure")) {
      dispatch(deleteProduct(id));
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (keyword.trim()) {
      history.push(`/admin/product/${keyword}`);
    } else {
      history.push("/admin/products");
    }
  };

  const products2 = products.filter((x) => x.user === userLogin._id);

  return (
    <>
      <AdminNavs />

      <header aria-label="Page Header" className="bg-gray-50">
        <div className="mx-auto max-w-screen-xl px-4 py-8 sm:px-6 lg:px-8">
          <div className="mt-8 pb-10">
            <h1 className="text-2xl font-bold text-gray-900 sm:text-3xl">
              All you products
            </h1>

            <p className="mt-1.5 text-sm text-gray-500">
              Search Products, Delete Products, Edit Products Keep it up! ðŸš€
            </p>
          </div>

          <div className="flex items-center ">
            <div className="relative hidden sm:block">
              <form onSubmit={handleSubmit}>
                <label className="sr-only" for="search">
                  {" "}
                  Search{" "}
                </label>

                <input
                  className="h-10 w-full rounded-lg border-none bg-white pl-4 pr-10 text-sm shadow-sm sm:w-56"
                  id="search"
                  type="search"
                  onChange={(e) => setKeyword(e.target.value)}
                  name="q"
                  placeholder="Search products..."
                />

                <button
                  type="button"
                  onClick={handleSubmit}
                  className="absolute top-1/2 right-1 -translate-y-1/2 rounded-md bg-gray-50 p-2 text-gray-600 transition hover:text-gray-700"
                >
                  <span className="sr-only">Submut Search</span>
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-4 w-4"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                    stroke-width="2"
                  >
                    <path
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                    />
                  </svg>
                </button>
              </form>
            </div>
          </div>
        </div>
      </header>

      {/* table */}
      <section className="bg-gray-50">
        <div className="container  px-5 py-10 mx-auto ">
          {loading ? (
            <p>loading...</p>
          ) : error ? (
            <p>{error}</p>
          ) : (
            <table className="min-w-full divide-y divide-gray-200 text-sm">
              <thead className="bg-gray-200">
                <tr>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    ID
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    NAME
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    PRICE
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    CATEGORY
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    BRAND
                  </th>
                </tr>
              </thead>

              <tbody className="divide-y divide-gray-200">
                {products2.length === 0 ? (
                  <p>No products</p>
                ) : (
                  products2.map((x) => (
                    <>
                      <tr key={x._id}>
                       
                        <td  className="whitespace-nowrap px-4 py-2 font-medium text-gray-900" >{x._id}</td>
                        <td  className="whitespace-nowrap px-4 py-2 font-medium text-gray-900" >{x.name}</td>
                        <td  className="whitespace-nowrap px-4 py-2 font-medium text-gray-900" > PKR {x.price} </td>
                        <td  className="whitespace-nowrap px-4 py-2 font-medium text-gray-900" >{x.category}</td>
                        <td  className="whitespace-nowrap px-4 py-2 font-medium text-gray-900" >{x.brandName ? x.brandName : x.brand}</td>
                        <td  className="whitespace-nowrap px-4 py-2 font-medium text-gray-900" >
                          <Link to={`/admin/product/${x._id}/edit`}>
                            <button
                              variant="primary"
                              classNameName="btn-sm"
                              // onClick={producct}
                            >
                              {/* <EditOutlined /> */}
                            <BiEdit color="blue" size={20} />
                            </button>
                          </Link>

                          <button
                            variant="danger"
                            classNameName="btn-sm"
                            onClick={() => {
                              deleteHandler(x._id);
                              window.location.reload();
                            }}
                          >
                            <i
                              classNameName="fas fa-trash"
                              style={{ color: "red" }}
                            ></i>
                          </button>
                        </td>

                      </tr>
                    </>
                  ))
                )}
              </tbody>
            </table>
          )}
        </div>
      </section>
    </>
  );
};

export default AdminProducts;
